.home
    height: 100vh

    &__wrapper
        width: 100%
        height: 100%
        position: relative
        overflow: hidden
        background: url('../img/home/home-bg.jpg') center/cover no-repeat
        background-attachment: fixed

    &__text
        position: absolute
        bottom: 0
        left: 50%
        transform: translateX(-50%)
        width: 100%
        text-align: center
        z-index: 0

    &__overhead
        font-size: 50px
        text-shadow: 0 5px 10px rgba(0, 0, 0, 0.7)
        margin-bottom: 5px
        @media (max-height: 700px)
            font-size: 40px
        @media (max-width: $md1)
            font-size: 40px
        @media (max-width: $md2)
            font-size: 35px
        @media (max-width: 600px)
            font-size: 28px
        @media (max-width: $md4)
            font-size: 22px

    &__title
        color: $blue-color
        font-size: 100px
        font-weight: 700
        text-shadow: 0 5px 10px rgba(0, 0, 0, 0.7)
        @media (max-height: 700px)
            font-size: 80px
        @media (max-width: $md1)
            font-size: 80px
        @media (max-width: $md2)
            font-size: 70px
        @media (max-width: $md3)
            font-size: 60px
        @media (max-width: 600px)
            font-size: 46px
        @media (max-width: $md4)
            font-size: 32px

    #tree
        position: absolute
        bottom: 0
        left: 0
        height: 20%
        width: 100%
        z-index: 10
        @media (max-width: $md4)
            height: 14%

    #mountain
        position: absolute
        bottom: 0
        left: 0
        height: 40%
        width: 100%
        z-index: 5
        @media (max-width: $md4)
            height: 29%

    #moon
        position: absolute
        top: 20%
        left: 50%
        transform: translateX(-50%)
        width: 30vw
        z-index: 1
        @media (max-height: 700px)
            width: 25vw
        @media (max-width: $md2)
            top: 27%
        @media (max-width: $md4)
            width: 40vw

    #cloud-left
        position: absolute
        top: 35%
        left: 0
        width: 40vw
        z-index: 2
        @media (min-height: 1300px)
            top: 28%
        @media (max-width: $md4)
            width: 45vw

    #cloud-right
        position: absolute
        top: 15%
        right: 0
        width: 40vw
        z-index: 2
        @media (min-height: 1300px)
            top: 18%
        @media (max-width: $md2)
            top: 28%
        @media (max-width: $md4)
            width: 45vw
