* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a:focus,
button:focus {
  outline: 2px solid #0097e6;
}

a {
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  line-height: 1;
  font-family: "Garnett", sans-serif;
  color: #fff;
  font-weight: 400;
  background: #040709;
  font-style: normal;
  scroll-behavior: smooth;
}

input,
button,
textarea {
  font-size: inherit;
  font-family: "Garnett", sans-serif;
}

button {
  cursor: pointer;
  background-color: transparent;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

.wrapper {
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
}
.main__about {
  padding: 100px 0 50px 0;
}
@media (max-width: 768px) {
  .main__about {
    padding: 50px 0 30px 0;
  }
}
.main__products, .main__services, .main__contact, .main__blog {
  padding: 50px 0;
}
@media (max-width: 992px) {
  .main__products, .main__services, .main__contact, .main__blog {
    padding: 40px 0;
  }
}
@media (max-width: 480px) {
  .main__products, .main__services, .main__contact, .main__blog {
    padding: 30px 0;
  }
}
@media (max-width: 480px) {
  .main__blog {
    padding-bottom: 5px;
  }
}

.container {
  max-width: 1220px;
  padding: 0 15px;
  margin: 0 auto;
}
@media (max-width: 768px) {
  .container {
    padding: 0 10px;
  }
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #040709;
  background-color: #fff;
  font-size: 18px;
  font-weight: 700;
}
.btn span {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  transition: transform 0.5s ease;
}
.btn span::before {
  content: attr(data-attr);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: #040709;
  border: 2px solid #040709;
  transition: transform 0.5s ease;
  transform-origin: top;
  transform: rotateX(90deg) translateY(-50%);
}
.btn span::after {
  content: attr(data-attr);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #040709;
  background-color: #fff;
  border: 2px solid #040709;
  transform-origin: bottom;
  transform: rotateX(0deg) translateY(0%);
  transition: transform 0.5s ease;
}
@media (any-hover: hover) {
  .btn:hover span::before {
    transform: rotateX(0deg) translateY(0%);
    transition: transform 0.5s ease;
  }
  .btn:hover span::after {
    transform: rotateX(90deg) translateY(50%);
    transition: transform 0.5s ease;
  }
}

.btn-double {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 5px;
}
.btn-double span {
  position: relative;
  padding: 10px 10px 0 10px;
  transition: transform 0.5s ease;
}
.btn-double span:nth-child(1) {
  color: #fff;
  background-color: #666;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.btn-double span:nth-child(1)::before {
  content: attr(data-attr);
  position: absolute;
  top: 0;
  left: 0;
  background-color: #0097e6;
  padding: 10px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  transition: transform 0.5s ease;
  transform-origin: top;
  transform: rotateX(90deg) translateY(-50%);
}
.btn-double span:nth-child(1)::after {
  content: attr(data-attr);
  padding: 10px;
  position: absolute;
  top: 0;
  left: 0;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  background: #666;
  transform-origin: bottom;
  transform: rotateX(0deg) translateY(0%);
  transition: transform 0.5s ease;
}
.btn-double span:nth-child(2) {
  color: #fff;
  background-color: #0097e6;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}
.btn-double span:nth-child(2)::before {
  content: attr(data-attr);
  position: absolute;
  top: 0;
  left: 0;
  background-color: #666;
  padding: 10px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  transition: transform 0.5s ease;
  transform-origin: bottom;
  transform: rotateX(90deg) translateY(50%);
}
.btn-double span:nth-child(2)::after {
  content: attr(data-attr);
  position: absolute;
  top: 0;
  left: 0;
  background-color: #0097e6;
  padding: 10px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  transition: transform 0.5s ease;
  transform-origin: top;
  transform: rotateX(0deg) translateY(0%);
}
.btn-double.hold {
  opacity: 0.2;
  cursor: default;
}
@media (any-hover: hover) {
  .btn-double:hover span:nth-child(1)::before {
    transform: rotateX(0deg) translateY(0%);
    transition: transform 0.5s ease;
  }
  .btn-double:hover span:nth-child(2)::before {
    transform: rotateX(0deg) translateY(0%);
    transition: transform 0.5s ease;
  }
  .btn-double:hover span:nth-child(1)::after {
    transform: rotateX(90deg) translateY(50%);
    transition: transform 0.5s ease;
  }
  .btn-double:hover span:nth-child(2)::after {
    transform: rotateX(-90deg) translateY(-50%);
    transition: transform 0.5s ease;
  }
}

.flyImage {
  opacity: 1;
  z-index: 100;
  position: fixed;
  transition: all 1.5s ease 0s;
}

.title {
  font-size: 40px;
  line-height: 130%;
}
.title span {
  color: #0097e6;
}
@media (max-width: 768px) {
  .title {
    font-size: 30px;
  }
}

.hidden {
  overflow: hidden;
}

.policy {
  padding: 20px;
}
.policy p {
  line-height: 120%;
}
.policy p:not(.policy p:last-child) {
  margin-bottom: 10px;
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #040709;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease 0s;
  opacity: 1;
  visibility: visible;
}
.preloader img {
  width: 250px;
  height: 200px;
}
.preloader.done {
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease 0s;
}

.checkbox {
  appearance: none;
}
.checkbox:checked + .checkbox-tag::after {
  transform: scale(1);
}
.checkbox input::-webkit-outer-spin-button,
.checkbox input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.checkbox-tag {
  position: relative;
  display: inline-block;
  color: #666;
  font-size: 14px;
  padding-left: 25px;
}
.checkbox-tag::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  border: 2px solid #0097e6;
}
.checkbox-tag::after {
  transition: all 0.3 ease 0s;
  position: absolute;
  content: "";
  top: 3px;
  left: 3px;
  width: 9px;
  height: 9px;
  background-color: #0097e6;
  transform: scale(0);
}
.checkbox-tag a {
  color: #0097e6;
}

.select__header {
  width: 100%;
  height: 100%;
  cursor: pointer;
  padding: 5px 10px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.select__current, .select__item {
  font-size: 14px;
  line-height: 120%;
  font-weight: 600;
  color: #666;
  padding-right: 10px;
}
.select__icon {
  color: #666;
  font-size: 14px;
  transition: all 0.3s ease 0s;
}
.select__icon.active {
  transform: rotate(-180deg);
}
.select__body {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background-color: #fff;
  display: none;
  z-index: 10;
}
.select__item {
  cursor: pointer;
  padding: 5px 5px 5px 15px;
  border: 1px solid #666;
  transition: all 0.3s ease 0s;
}
@media (any-hover: hover) {
  .select__item:hover {
    background-color: #dbdbdb;
  }
}

