.header {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 1000;
  transition: box-shadow 0.3s ease 0s, background-color 0.3s ease 0s;
}
.header.active {
  box-shadow: 0 0 40px rgba(255, 255, 255, 0.8);
  background-color: rgba(0, 0, 0, 0.8);
}
@media (any-hover: hover) {
  .header:hover {
    background-color: rgba(0, 0, 0, 0.9);
  }
}
.header__body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 90px;
  width: 100%;
  padding: 10px 0;
}
.header__logo {
  flex: 0 0 45px;
  padding-right: 10px;
  display: flex;
  align-items: center;
}
.header__logo-icon {
  font-size: 40px;
  color: #fff;
  margin-right: 10px;
}
.header__logo-name {
  font-size: 30px;
  font-weight: bold;
  color: #0097e6;
}
@media (max-width: 480px) {
  .header__logo-name {
    font-size: 25px;
  }
}
@media (min-width: 992px) {
  .header__menu {
    flex: 0 1 750px;
  }
}
@media (max-width: 992px) {
  .header__menu {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    transform: translateX(-110%);
    width: 65%;
    height: 100vh;
    overflow: auto;
    z-index: 10;
    background-color: rgba(0, 0, 0, 0.85);
    transition: transform 0.5s ease 0s;
    padding: 80px 10px 30px 40px;
    box-shadow: 0 0 40px rgba(255, 255, 255, 0.8);
  }
  .header__menu.active {
    transform: translateX(0);
  }
}
@media (max-width: 480px) {
  .header__menu {
    width: 85%;
  }
}
.header__list {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  padding-right: 20px;
}
@media (max-width: 992px) {
  .header__list {
    flex-direction: column;
    align-items: flex-start;
    justify-content: stretch;
    flex-wrap: nowrap;
  }
}
@media (max-width: 480px) {
  .header__list {
    margin-bottom: 40px;
  }
}
@media (max-width: 992px) {
  .header__item {
    margin: 0 0 20px 0;
  }
  .header__item:last-child {
    margin: 0;
  }
}
.header__link {
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  background-color: transparent;
  color: #040709;
  padding: 7px;
}
.header__link span {
  padding: 7px;
}
.header__link span::before {
  padding: 7px;
  background-color: #fff;
  color: #040709;
  border: none;
}
.header__link span::after {
  padding: 7px;
  color: #fff;
  background-color: transparent;
  border: none;
}
@media (max-width: 992px) {
  .header__link {
    font-size: 40px;
    font-weight: 700;
  }
}
@media (max-width: 480px) {
  .header__link {
    font-size: 25px;
  }
}
.header__icons {
  display: none;
}
@media (max-width: 480px) {
  .header__icons {
    padding: 0 14px;
    display: flex;
    align-items: center;
  }
}
.header__actions {
  display: flex;
  align-items: center;
}
.header__user, .header__search-btn,
.header .cart-header__btn {
  color: #fff;
  font-size: 25px;
}
@media (any-hover: hover) {
  .header__user:hover, .header__search-btn:hover,
.header .cart-header__btn:hover {
    color: #0097e6;
  }
}
.header__user {
  margin-right: 20px;
  font-size: 30px;
}
@media (max-width: 480px) {
  .header__user {
    font-size: 32px;
  }
}
.header__search-btn {
  margin-right: 20px;
}
@media (max-width: 480px) {
  .header__search-btn {
    margin-right: 15px;
  }
}
.header__cart {
  position: relative;
  margin-right: 15px;
}
@media (any-hover: hover) {
  .header__cart.active:hover .cart-header__content {
    opacity: 1;
    visibility: visible;
    transition: all 0.5s ease 0s;
  }
}
.header__burger {
  display: none;
}
@media (max-width: 992px) {
  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 18px;
    cursor: pointer;
    z-index: 5;
    margin-left: 20px;
    background-color: #040709;
    transition: background-color 0.3s ease 0s;
  }
  .header__burger span {
    transition: all 0.3s ease 0s;
    top: calc(50% - 1px);
    left: 0;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #fff;
  }
  .header__burger span:first-child {
    top: 0;
  }
  .header__burger span:last-child {
    top: auto;
    bottom: 0;
  }
  .header__burger.active span {
    transform: scale(0);
  }
  .header__burger.active span:first-child {
    transform: rotate(-45deg);
    top: calc(50% - 1px);
  }
  .header__burger.active span:last-child {
    transform: rotate(45deg);
    bottom: calc(50% - 1px);
  }
}
@media (max-width: 992px) and (any-hover: hover) {
  .header__burger:hover span {
    background-color: #0097e6;
  }
}
@media (max-width: 992px) and (max-width: 480px) {
  .header__burger {
    margin-left: 15px;
  }
}
.header__search {
  position: fixed;
  top: 0;
  right: 0;
  background-color: #fff;
  box-shadow: 0 0 40px rgba(255, 255, 255, 0.8);
  position: absolute;
  width: 30%;
  height: 100vh;
  overflow: auto;
  padding: 20px;
  transform: translateX(110%);
  transition: all 0.5s ease 0s;
  opacity: 0;
  visibility: hidden;
  z-index: 10;
}
.header__search.active {
  transform: translateX(0);
  opacity: 1;
  visibility: visible;
  transition: all 0.5s ease 0s;
}
@media (max-width: 1200px) {
  .header__search {
    width: 40%;
    transition: all 0.5s ease 0s;
  }
}
@media (max-width: 480px) {
  .header__search {
    padding: 15px;
    width: 90%;
  }
}

