.categories-search__title {
  color: #040709;
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 25px;
}
.categories-search__list {
  display: inline-block;
}
.categories-search__item:not(.categories-search__item:last-child) {
  margin-bottom: 10px;
}
.categories-search__btn {
  width: 160px;
  height: 50px;
}
@media (max-width: 768px) {
  .categories-search__btn {
    width: 140px;
    height: 45px;
  }
}

.cart-header__content {
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s ease 0s;
  position: absolute;
  right: -20px;
  top: 230%;
  width: 345px;
  max-height: 250px;
  background-color: #f3f3f3;
}
.cart-header__btn {
  position: relative;
}
.cart-header__btn span {
  display: none;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  right: -4px;
  padding: 3px 11px;
  height: 20px;
  border-radius: 20px;
  background-color: #0097e6;
  color: #fff;
  transform: translate(60%, -40%);
  font-size: 14px;
  font-weight: 600;
}
.cart-header__btn span.active {
  display: inline-flex;
}

.cart-content {
